import Head from 'next/head';
import houses from '../../houses.json';

const House = props => (
  <div>
    <Head>
      <title>{props.dada.title}</title>
    </Head>
    <img src={props.dada.picture} width='50%' alt='House picture' />
    <p>
      {props.dada.type} - {props.dada.town}
    </p>
    <p>{props.dada.title}</p>
    <p>
      {props.dada.rating} ({props.dada.reviewsCount})
    </p>
  </div>
);

House.getInitialProps = ({ query }) => {
  console.log(query);
  const { id } = query;

  return {
    dada: houses.filter(house => house.id === id)[0],
  };
};

export default House;
